	formatOrderTime(str){
		if(!(str)||str.length!=19) return str;
		return str.substring(0,16);
	}
	//  IT icon
	getICON(subProcess){
		let icon = 'icon-device-icon';
		switch( subProcess ){
			case '12': // 
			case '24': // 
			case '25': // 
			case '26': // 
			case '33': // 
			case '35': // 
			case '41': // 
			case '42': // 
			case '43': // 
			// case '43-1': //  
			case '43-2': //  
			case '51': // //
				icon = 'icon-project-icon';
				break;
			case '13': // IT
			case '34': // IT
			case '36': // IT
			case '43-3': //  IT
			case '53': // /IT
			case '53': // /IT
			case '55': // /IT
			// case '56': // /IT  20171024
				icon = 'icon-it-icon';
				break;
			case '61': // /GC
				icon = 'icon-gc-icon';
				break;
		}
		return icon;
	}
